import React, { useState, useEffect } from 'react';
import * as RB from 'react-bootstrap';
import ClimbingBoxLoader from 'react-spinners/ClimbingBoxLoader';
import SignUpModal from '../../components/SignUpModal';
import SignInModal from '../../components/SignInModal';
import OffCanvasmenu from '../../components/OffCanvasmenu';
import Header from '../../components/Header';
import * as RC from 'react-icons/cg';
import Border from '../../components/Border';
import Footer from '../../components/Footer';
import ReactDOM from 'react-dom';
import CartImg from '../../assets/images/Others/Cart.svg';
import rating from '../../assets/images/Others/rating.png';
import { list, remove, quantity, total } from 'cart-localstorage';
import MessengerCustomerChat from 'react-messenger-customer-chat';


function Cart() {
	const [ isloading, setLoading ] = useState(true);
	const [ isMenuOpen, openMenu ] = useState(false);
	const [ data ] = useState(list());
	const [ isSignUpOpen, OpenisSignUp ] = useState(false);
	const [ isSignInOpen, OpenisSignIn ] = useState(false);


	const CloseSignUp = () => {
		OpenisSignUp(false);
	};
	useEffect(() => {
		setLoading(false);
	}, []);

	const openOffMenu = () => {
		openMenu(true);
	};
	const CloseSignIn = () => {
		OpenisSignIn(false);
	};
	console.log(total());
	const AddItems = (e) => {
		quantity(Number(e.target.id), +1);
		window.location.reload();
	};
	const RemItems = (e) => {
		quantity(Number(e.target.id), -1);
		window.location.reload();
	};

	const DelItem = (e) => {
		remove(Number(e.target.id));
		window.location.reload();
	};

	return (
		<div className={`${isloading ? 'd-flex justify-content-center Home-Loading' : ' '}`}>
			{isloading ? (
				<ClimbingBoxLoader color="#45a29e" className="HomeLoader" />
			) : (
				<RB.Container fluid className="HomeCont">
					{/* Loader Ends */}
					<RB.Row>
						{/* Main Row Starts */}
						<RB.Col
							lg={3}
							xl={3}
							className={`${isMenuOpen ? 'd-block w-md-25 smallWidth' : 'd-none'} d-lg-block noPad`}
						>
							<OffCanvasmenu openMenu={openMenu} />
						</RB.Col>
						{/* Off Canvas Menu Ends */}
						<RB.Col
							md={2}
							className={`${isMenuOpen ? 'd-none' : ' '} d-block d-lg-none position-fixed homeMenu`}
						>
							<span>
								<RC.CgMenuGridO onClick={openOffMenu} />
							</span>
						</RB.Col>
						{/* Off Canvas Mobile Button Ends */}

						{/* OffCanvas Ends */}
						<RB.Col lg={9} xs={12} className={`${isMenuOpen ? 'HomeBlured' : ' '}`}>
							<Header name="Cart" SignIn={OpenisSignIn} SignUp={OpenisSignUp} />
							{/* Main Header Ends */}

							<RB.Row className="w-100">
								<RB.Col xs={12} className="fw-bold display-6 ms-0 ms-mb-3 mt-4 mb-4 ">
									<Border width="100%">
										<div className="d-flex justify-content-between HomeSales">
											<p className="mt-4 HomeHeader fs-1 ms-md-5 mt-4">Your Cart</p>
											<p>
												<RB.Image
													src={CartImg}
													width="200px"
													height="200px"
													className="d-none d-md-block ms-4 me-4"
												/>
												<RB.Image
													src={CartImg}
													width="150px"
													height="150px"
													className="d-md-none me-4"
												/>
											</p>
										</div>
									</Border>
								</RB.Col>
							</RB.Row>
							{/* Headerr Sections Ends */}
							<RB.Row>
								<RB.Col sm={12}>
									{data.map((e) => {
										return (
											<div className="mt-5" key={e.id} >
												<Border>
													<RB.Container fluid className="pt-3 pb-3 TrendingContainer">
														<RB.Row>
															<RB.Col sm={4}>
																<RB.Image
																	src={e.Image}
																	width={e.width}
																	height={e.height}
																	className="ms-4 ms-md-0 pt-0 pt-md-3"
																/>{' '}
															</RB.Col>
															<RB.Col sm={8}>
																{' '}
																<RB.Row className="TrendingNameRow">
																	<RB.Col
																		sm={12}
																		className="display-6 fs-3 mt-5 ms-3 ms-md-0 TrendingName fw-bold"
																	>
																		{' '}
																		<span
																			className="TrendingCursor d-none d-md-block"
																			id={e.id}
																		>
																			{' '}
																			{e.name}{' '}
																		</span>{' '}
																		<span className="d-block d-md-none ms-4 ms-md-0"> {e.name} </span>
																	</RB.Col>
																</RB.Row>
																<RB.Row>
																	<RB.Col
																		sm={12}
																		className="d-md-flex justify-content-start mt-4 fw-bold fs-4"
																	>
																		<RB.Button
																			variant="outline-success"
																			id={e.id}
																			className="btn-sm me-3"
																			onClick={AddItems}
																		>
																			{' '}
																			Add Another
																		</RB.Button>
																		{e.quantity}{' '}
																		<RB.Button
																			variant="outline-danger"
																			id={e.id}
																			className="btn-sm ms-3"
																			onClick={RemItems}
																		>
																			Remove One
																		</RB.Button>
																	</RB.Col>
																</RB.Row>
																<RB.Row className="TrendingNameRow">
																	<RB.Col
																		sm={12}
																		className="fw-bold mt-3 ms-4 ms-md-0"
																	>
																		{' '}
																		<RB.Image src={rating} width={50} /> {e.Rating}{' '}
																	</RB.Col>
																</RB.Row>{' '}
																<RB.Row className="TrendingNameRow">
																	<RB.Col
																		sm={12}
																		className="fw-bold display-6 fs-4 text-danger mt-3 ms-4 ms-md-0"
																	>
																		{e.price * e.quantity} $
																	</RB.Col>
																	<RB.Col
																		sm={12}
																		className="d-md-flex justify-content-start"
																	>
																		{' '}
																		<RB.Button
																			variant="danger"
																			className="mt-5 btn-sm ms-4 ms-md-0"
																			id={e.id}
																			onClick={DelItem}
																		>
																			{' '}
																			Remove Item{' '}
																		</RB.Button>{' '}
																	</RB.Col>
																</RB.Row>
															</RB.Col>
														</RB.Row>
													</RB.Container>
												</Border>
											</div>
										);
									})}
								</RB.Col>
							</RB.Row>
							<RB.Row>
								<RB.Col sm={12} className="d-md-flex justify-content-around fw-bold fs-4 mt-5 ms-5 ms-md-0">
									<p className="HomeHeader"> Total Amount </p>
									<p> {total()} $ </p>
								</RB.Col>
							</RB.Row>

							<RB.Row>
								<RB.Col sm={12}>
									<div className="d-flex justify-content-between w-100 ps-5 pe-5 mt-5">
										<RB.Button variant="outline-success"> Add Payment Methods </RB.Button>
										<RB.Button variant="dark"> Buy All </RB.Button>
									</div>
								</RB.Col>
							</RB.Row>
							<br />
							<Footer />
							<br />
						</RB.Col>
						{/* Main Col Ends */}
					</RB.Row>
					{/* Main Row Ends */}
				</RB.Container>
				//Main Container Ends
			)}

			{isSignUpOpen ? (
				ReactDOM.createPortal(<SignUpModal CloseSignUp={CloseSignUp} />, document.getElementById('SignUp'))
			) : null}
			{isSignInOpen ? (
				ReactDOM.createPortal(<SignInModal CloseSignIn={CloseSignIn} />, document.getElementById('SignIn'))
			) : null}
			<MessengerCustomerChat pageId="111296841371902" appId="3041956499423319" />
		</div>
	);
}

export default Cart;
