import '../assets/css/pages/Home.css';
import * as RB from 'react-bootstrap';
import * as RC from 'react-icons/cg';
import Header from '../components/Header';
import ReactDOM from 'react-dom';
import SignUpModal from '../components/SignUpModal';
import SignInModal from '../components/SignInModal';
import OffCanvasmenu from '../components/OffCanvasmenu';
import ClimbingBoxLoader from 'react-spinners/ClimbingBoxLoader';
import { useState, useEffect } from 'react';
import CodeSnippets from '../components/CodeSnippets';
import Border from '../components/Border';
import CompImage from '../assets/images/Others/Components.svg';
import ChartImg from '../assets/images/Others/guy_with_glasses.svg';
import Footer from '../components/Footer';
import BooksImage from '../assets/images/Others/Books.svg';
import MessengerCustomerChat from 'react-messenger-customer-chat';

function Components() {
	const [ isMenuOpen, openMenu ] = useState(false);
	const [ isloading, setLoading ] = useState(true);
	useEffect(() => {
		setLoading(false);
	}, []);
	const openOffMenu = () => {
		openMenu(true);
		console.log(isMenuOpen);
	};
	const [ isSignUpOpen, OpenisSignUp ] = useState(false);
	const [ isSignInOpen, OpenisSignIn ] = useState(false);
	const CloseSignUp = () => {
		OpenisSignUp(false);
	};
	const CloseSignIn = () => {
		OpenisSignIn(false);
	};
	let Code = `<Highlight {...defaultProps} theme={theme} code={code} language="jsx">
	{({ className, style, tokens, getLineProps, getTokenProps }) => (
	  <Fragment>
		{tokens.map((line, i) => (
		  <div {...getLineProps({ line, key: i })}>
			{line.map((token, key) => <span {...getTokenProps({ token, key })} />)}
		  </div>
		))}
	  </Fragment>
	)}
  </Highlight>`;

	let OffCode = `<div className="OffCanvasMenu mt-2 text-muted">
  <ul className="pt-4">
<li>
<NavLink to="/" activeclassname="Active" className="OffCanvasList">
<span className="OffCanvasLeft">
<RI.BsArrowRightSquare />
</span>
<span className="OffCanvasRight"> Dashboard</span>
</NavLink>
</li>
<li>
<NavLink to="/Trending" className="OffCanvasList">
<span className="OffCanvasLeft">
<RI.BsArrowRightSquare /> </span>`;

	let BarChart = `this.state = {
series: [{  data: [ 34, 44, 54, 21, 12, 43, 33, 23, 66, 66, 58 ]}],options: {
chart: {type: 'line',height: 350},stroke: {curve: 'stepline'},
dataLabels: {enabled: false},title: {text: '',align: 'left'}, markers: {hover: {sizeOffset: 4}
}}}; }
render() {
return (
<div id="chart">
 <ReactApexChart options={this.state.options} series={this.state.series} type="line" height={300} />
</div>
);}}`;
	let RadialChart = ` 
series: [ 75 ],
plotOptions: {
radialBar: {startAngle: -135,endAngle: 225,hollow: {margin: 0,size: '70%',background: '#1f2833',image: undefined,imageOffsetX: 0,imageOffsetY: 0,position: 'front',
dropShadow: {enabled: true,top: 3,left: 0,blur: 4,opacity: 0.24}},
track: {background: '#fff',strokeWidth: '67%',margin: 0, // margin is in pixelsdropShadow: {enabled: true,top: -3,left: 0,blur: 4,opacity: 0.35}},
dataLabels: {show: true,name: {offsetY: -10,show: true,color: '#66fcf1',fontSize: '17px'},value:{formatter: function(val){return parseInt(val);},`;

	let Shop1 = `<div style={FilterDiv}>
<div className="d-flex justify-content-between">
<p className="display-6 ps-2 pt-3 HomeHeader fw-bold"> Filters </p>
<RB.CloseButton className="pt-5 mt-1 me-3" onClick={CloseFilterDiv} />
</div>
<p className="display-6 fs-5 pt-4"> Search By Price </p>
<p><RangeSlider max={50000} variant="success" className="w-100" value={value} onChange={(changeEvent) => 
setValue(changeEvent.target.value)}
<RB.Container fluid className="pt-3 pb-3 TrendingContainer">
<RB.Row`;
	let Shop2 = `
<div className="mt-5" key={e.id} >
</div>
<p className="display-6 fs-5 pt-4"> Search By Price </p>
<Border>
<RB.Container fluid className="pt-3 pb-3 TrendingContainer">
<RB.Row>
<RB.Col sm={4}>
<RB.Image src={e.Image} width={e.width} height={e.height} className="ms-4 ms-md-0 pt-0 pt-md-3" />
</RB.Col>
<RB.Col sm={8}>
<RB.Row className="TrendingNameRow">
<RB.Col sm={12} className="display-6 fs-3 mt-5 ms-3 ms-md-0 TrendingName fw-bold">`;
	return (
		<div className={`${isloading ? 'd-flex justify-content-center Home-Loading' : ' '}`}>
			{isloading ? (
				<ClimbingBoxLoader color="#45a29e" className="HomeLoader" />
			) : (
				<RB.Container fluid className="HomeCont">
					{/* Loader Ends */}
					<RB.Row>
						{/* Main Row Starts */}
						<RB.Col
							lg={3}
							xl={3}
							className={`${isMenuOpen ? 'd-block w-md-25 smallWidth' : 'd-none'} d-lg-block noPad`}
						>
							<OffCanvasmenu openMenu={openMenu} />
						</RB.Col>
						{/* Off Canvas Menu Ends */}
						<RB.Col
							md={2}
							className={`${isMenuOpen ? 'd-none' : ' '} d-block d-lg-none position-fixed homeMenu`}
						>
							<span>
								<RC.CgMenuGridO onClick={openOffMenu} />
							</span>
						</RB.Col>
						{/* Off Canvas Mobile Button Ends */}

						{/* OffCanvas Ends */}
						<RB.Col lg={9} xs={12} className={`${isMenuOpen ? 'HomeBlured' : ' '}`}>
							<Header name="Components" SignIn={OpenisSignIn} SignUp={OpenisSignUp} />
							{/* Main Header Ends */}
							{/* First Row */}
							<RB.Row>
								<RB.Col sm={12} className=" mt-3 mt-md-0">
									<Border>
										<div className="d-flex justify-content-between HomeSales ">
											<p className="mt-4 HomeHeader fs-1 ms-md-5 fw-bold">Code Snippets</p>
											<p>
												<RB.Image
													src={CompImage}
													width="200px"
													height="200px"
													className="d-none d-md-block ms-4 me-4"
												/>
												<RB.Image
													src={CompImage}
													width="150px"
													height="150px"
													className="d-md-none "
												/>
											</p>
										</div>
									</Border>
								</RB.Col>
							</RB.Row>
							{/* First Row 2 */}
							<RB.Row className="mt-5">
								<RB.Col sm={12} md={6}>
									<Border>
										<div className="text-start">
											<CodeSnippets>{Code}</CodeSnippets>
											<hr />
										</div>
										<span className="HomeHeader fw-bold fs-4"> Code For The Editor </span>
									</Border>
								</RB.Col>
								<RB.Col sm={12} md={6} className="mt-5 mt-md-0 mb-4 mb-md-0">
									<Border>
										<div className="text-start">
											<CodeSnippets>{OffCode}</CodeSnippets>
											<hr />
										</div>
										<span className="HomeHeader fw-bold fs-4"> Off Canvas Menu </span>
									</Border>
								</RB.Col>
							</RB.Row>
							{/* Second Row */}
							<RB.Row className="mt-5 mb-5">
								<RB.Col sm={12}>
									<Border>
										<div className="d-flex justify-content-between HomeSales">
											<p>
												<RB.Image
													src={ChartImg}
													width="200px"
													height="200px"
													className="d-none d-md-block ms-4 me-4"
												/>
												<RB.Image
													src={ChartImg}
													width="150px"
													height="150px"
													className="d-md-none "
												/>
											</p>
											<p className="mt-4 HomeHeader fs-1 me-md-5 fw-bold">Charts</p>
										</div>
									</Border>
								</RB.Col>
							</RB.Row>
							{/* Second Row 2 */}
							<RB.Row>
								<RB.Col sm={12} md={6} className="mt-5 mt-md-0 mb-4 mb-md-0">
									<Border>
										<div className="text-start">
											<CodeSnippets>{BarChart}</CodeSnippets>
											<hr />
										</div>
										<span className="HomeHeader fw-bold fs-4"> Line Charts On Dashboard </span>
									</Border>
								</RB.Col>
								<RB.Col sm={12} md={6}>
									<Border>
										<div className="text-start">
											<CodeSnippets>{RadialChart}</CodeSnippets>
											<hr />
										</div>
										<span className="HomeHeader fw-bold fs-4"> Radial Charts On Dashboard </span>
									</Border>
								</RB.Col>
							</RB.Row>
							{/* Third Row */}
							<RB.Row className="mt-5 mb-5">
								<RB.Col sm={12}>
									<Border>
										<div className="d-flex justify-content-between HomeSales">
											<p className="mt-4 HomeHeader fs-1 ms-md-5 fw-bold">Shop</p>
											<p>
												<RB.Image
													src={BooksImage}
													width="200px"
													height="200px"
													className="d-none d-md-block ms-4 me-4"
												/>
												<RB.Image
													src={BooksImage}
													width="150px"
													height="150px"
													className="d-md-none "
												/>
											</p>
										</div>
									</Border>
								</RB.Col>
							</RB.Row>

							<RB.Row>
								<RB.Col sm={12} md={6}>
									<Border>
										<div className="text-start">
											<CodeSnippets>{Shop1}</CodeSnippets>
											<hr />
										</div>
										<span className="HomeHeader fw-bold fs-4"> Code On Books Page </span>
									</Border>
								</RB.Col>
								<RB.Col sm={12} md={6} className="mt-5 mt-md-0 mb-4 mb-md-0">
									<Border>
										<div className="text-start">
											<CodeSnippets>{Shop2}</CodeSnippets>
											<hr />
										</div>
										<span className="HomeHeader fw-bold fs-4"> Code On Cart Page </span>
									</Border>
								</RB.Col>
							</RB.Row>
							{/* Footer */}
							<br />
							<Footer />
							<br />
						</RB.Col>
						{/* Main Col Ends */}
					</RB.Row>
					{/* Main Row Ends */}
				</RB.Container>
				//Main Container Ends
			)}

			{isSignUpOpen ? (
				ReactDOM.createPortal(<SignUpModal CloseSignUp={CloseSignUp} />, document.getElementById('SignUp'))
			) : null}
			{isSignInOpen ? (
				ReactDOM.createPortal(<SignInModal CloseSignIn={CloseSignIn} />, document.getElementById('SignIn'))
			) : null}
			<MessengerCustomerChat pageId="111296841371902" appId="3041956499423319" />
		</div>
	);
}

export default Components;
