import '../assets/css/components/ProductMenu.css';
import '../assets/css/pages/Home.css';
import { Link } from 'react-router-dom';

import * as RB from 'react-bootstrap';
function ProductMenu(props) {
	const CloseProductMenu = () => {
		props.CloseProduct(false);
	};
	const Data = props.ProductMenuData;
	return (
		<div className="ProductMenuMain d-none d-md-flex">
			<div className='BorderProduct'  >
				<div className="ProductMenu fw-bold  ProductSales">
					<RB.Row>
						<RB.Col sm={12} className="display-6 ">
							
								<div className="d-flex justify-content-between ">
									<p className=" pt-3 HomeHeader fs-1 ms-md-3 fw-bold">Product Details</p>{' '}
									<span className="pt-4 fs-5 pe-4">
										<RB.CloseButton onClick={CloseProductMenu} variant="white" />
									</span>
								</div>
							
						</RB.Col>
					</RB.Row>
					<RB.Row>
						<RB.Col sm={4} className="mt-4 ps-4">
							{' '}
							<RB.Image src={Data.Image} height={Data.height} width={Data.width} className='mb-4' />{' '}
                            <span className=" fw-bold fs-5 ps-1"> Rating : { Data.Rating } </span>
						</RB.Col>
						<RB.Col sm={8} className="mt-5">
							{' '}
							<span className="ProductHeader fs-4"> {Data.first_name} </span>
							<RB.Row>
								<RB.Col sm={12} className="fw-light fs-5 mt-4">
									<div className="d-flex justify-content-center">
										<span className="fs-3 fw-bold"> Available Colors </span>{' '}
										<RB.Button
											variant="outline-secondary"
											disabled
											className="ms-4 fs-6 fw-bold text-primary w-50"
											size="sm"
										>
											{' '}
											Grey{' '}
										</RB.Button>
									</div>{' '}
								</RB.Col>{' '}
                                <RB.Col sm={6} className="mt-5 fw-bold fs-4"> <RB.Button variant="outline-success" className="w-100" > <Link to="/Shop/Cart"> Proceed To CheckOut </Link></RB.Button>  </RB.Col>
                                <RB.Col sm={6} className="mt-5 fw-bold fs-4"> <RB.Button variant="outline-warning" className="w-100" onClick={CloseProductMenu} > Go Back</RB.Button>  </RB.Col>
							</RB.Row>
							
						</RB.Col>
					</RB.Row>
				</div>
			</div>
		</div>
	);
}

export default ProductMenu;
