import * as RB from 'react-bootstrap';

function DocComp(props) {
    
    return ( 
        <div className="text-md-start p-4">
            <RB.Row>
                <RB.Col sm={12} >
                   
            <RB.Image src={ props.img} width={120} height={120} className={ ` ${ props.Name } `} />
            
            </RB.Col>
            </RB.Row>
            <RB.Row>
                <RB.Col sm={12}>
                    <div>
                    <span className='HomeHeader fs-3 fw-bold pt-2'> {props.Header}  </span>
                     </div>
                </RB.Col>
            </RB.Row>
            <RB.Row>
                <RB.Col sm={12}>
                    <div>
                    <span className='fs-4 pt-2'>  {props.para} </span>
                     </div>
                </RB.Col>
            </RB.Row>
        </div>
     );
}

export default DocComp;