import { TableData } from '../datafiles/Home/TableData';
import * as RB from 'react-bootstrap';
import "../assets/css/components/HomeTableComp.css"
function HomeTable() {
	return (
		<div>
		<div className='d-none d-md-block'>
         <RB.Row className="TableBorderMain display-md-6 fw-bolder ms-2 fs-md-5">
         <RB.Col sm={1} className="pt-md-2 pb-md-2"> ID </RB.Col>
             <RB.Col sm={1} className="ps-md-1 pt-md-2 pb-md-2"> Images </RB.Col>
             <RB.Col sm={2} className="ps-md-4 pt-md-2 pb-md-2"> Name </RB.Col>
             <RB.Col md={2} sm={0} ></RB.Col>
             <RB.Col sm={2} className=" pe-md-5 pt-md-2 pb-md-2"> Gender </RB.Col>
             <RB.Col sm={2} className="ps-md-4 pt-md-2 pb-md-2"> Designation </RB.Col>

         </RB.Row>
			{TableData.map((e) => {
				return (
					<RB.Row className="p-2 ps-3 TableBorder" key={e.id}>
                        <RB.Col sm={1} className="fs-5 pt-2 TableCol">
							{e.id}
						</RB.Col>
                            
						<RB.Col sm={1} className="ps-3" >
							{' '}
							<RB.Image src={e.img} height={40} width={40} roundedCircle />
						</RB.Col>
                        <RB.Col sm={4} className="fs-5 ps-2 pt-1 TableCol"> { e.first_name + " " +  e.last_name } </RB.Col>
                      
                        <RB.Col sm={1} className="fs-5 ms-2 pt-1 TableCol"> { e.gender } </RB.Col>
                       
                        <RB.Col sm={4} className="fs-5 pt-1 ps-5 ms-2 TableCol" > {e.Design} </RB.Col>
					</RB.Row>
				);
			})}
         <div className="display-6 mt-4 mb-3"> Customer Table </div>
		</div>
		<div className='d-block d-md-none'>
			<RB.Container>
				<RB.Row>
					<RB.Col sm={12} className="ms-4" style={{textAlign : "justify"}}> ID &nbsp;&nbsp; Images &nbsp; Name &nbsp; Gender</RB.Col>
				</RB.Row>
				{TableData.map(e =>{
					return(
						<RB.Row key={e.id}>
							<RB.Col sm={12} className="ms-4" style={{textAlign : "justify"}}> {e.id} &nbsp; <RB.Image src={e.img} height={20} width={20} roundedCircle /> &nbsp;  {e.first_name} &nbsp; {e.gender } &nbsp; </RB.Col>
						</RB.Row>
					)
				})}
			</RB.Container>
		</div>
		
    </div>
	);
}

export default HomeTable;
