import "../assets/css/components/Header.css";
import * as RB from "react-bootstrap";


function Header(props) {
   const OpenSignIn = () =>{
    props.SignIn(true);
   }
   const OpenSignUP = () =>{
      props.SignUp(true);
   }


   
    return ( 
        <RB.Container className="mt-2 mt-md-4 HeaderMain">
        
           
           <RB.Row>
           <RB.Col lg={10}>
           <p className="HeaderName w-100 ms-5 ms-md-0"> {props.name} </p>

           </RB.Col>
              <RB.Col lg={2}>
           <RB.ButtonGroup className="d-none d-md-flex w-100 mt-3">
           <RB.Button variant="outline-primary" className=" btn-sm" onClick={OpenSignIn}>Sign In</RB.Button>
           <RB.Button variant="outline-danger" className="ms-2  btn-sm" onClick={OpenSignUP}>Sign Up</RB.Button>
           </RB.ButtonGroup>

           <RB.ButtonGroup vertical className="d-flex d-md-none mt-3 VerticalHeader">
           <RB.Button variant="outline-primary" className="w-50 btn-sm" onClick={OpenSignIn}  >Sign In</RB.Button>
           <RB.Button variant="outline-danger" className="w-50 mt-2 btn-sm" onClick={OpenSignUP}>Sign Up</RB.Button>
           </RB.ButtonGroup>
           
           </RB.Col>
           </RB.Row>
          
        </RB.Container>
     );
}

export default Header;