import ReactApexChart from "react-apexcharts";
import React from 'react';

  class ApexChartMixed extends React.Component {
    constructor(props) {
      super(props);

      this.state = {
      
        series: [{
          name: 'Income',
          type: 'column',
          data: [1.4, 2, 2.5, 1.5, 2.5, 2.8]
        }, {
          name: 'Cashflow',
          type: 'column',
          data: [1.1, 3, 3.1, 4, 4.1, 4.9]
        }, {
          name: 'Revenue',
          type: 'line',
          data: [20, 29, 37, 36, 44, 45]
        }],
        options: {
          chart: {
            height: 330,
            type: 'line',
            stacked: false
          },
          dataLabels: {
            enabled: false
          },
          stroke: {
            width: [1, 1, 4]
          },
          title: {
            text: '',
            align: 'left',
            offsetX: 0
          },
          xaxis: {
            categories: [2009, 2010, 2011, 2012, 2013, 2014],
          },
          yaxis: [
            {
              axisTicks: {
                show: true,
              },
              axisBorder: {
                show: true,
                color: '#008FFB'
              },
              labels: {
                style: {
                  colors: '#66fcf1',
                }
              },
              title: {
                text: "Income",
                style: {
                  color: '#008FFB',
                }
              },
              tooltip: {
                enabled: true
              }
            },
            {
              seriesName: 'Income',
              opposite: true,
              axisTicks: {
                show: true,
              },
              axisBorder: {
                show: true,
                color: '#66fcf1'
              },
              labels: {
                style: {
                  colors: '#66fcf1',
                }
              },
              title: {
                text: "Operating Cashflow",
                style: {
                  color: '#66fcf1',
                }
              },
            }
        ],
          tooltip: {
            fixed: {
              enabled: true,
              position: 'topLeft', // topRight, topLeft, bottomRight, bottomLeft
              offsetY:100,
              offsetX: 120
            },
          },
          legend: {
            horizontalAlign: 'left',
            offsetX: 10
          }
        },
      
      
      };
    }

  

    render() {
      return (
        

  <div id="chart">
<ReactApexChart options={this.state.options} series={this.state.series} type="line" height={300}/>
</div>


      );
    }
  }

  export default ApexChartMixed;