import * as RB from 'react-bootstrap';
import '../assets/css/components/OffCanvasmenu.css';
import me1 from '../assets/images/myself/me1.jpg';
import * as RI from 'react-icons/bs';
import * as RS from 'react-icons/si';
import { NavLink } from 'react-router-dom';
import { useState } from 'react';


function OffCanvasmenu(props) {
	const [ secondMenu, openSecondMenu ] = useState(false);
	const [ thirdMenu, openThirdMenu ] = useState(false);
	const [ count, increaseCount ] = useState(0);
	const [ thirdCount, increaseThirdCount ] = useState(0);
	const [ meMenu, openMeMenu ] = useState(false);

	const MenuDown = () => {
		increaseCount(count + 1);

		if (count % 2 === 0) {
			openSecondMenu(true);
		} else {
			openSecondMenu(false);
		}
	};

	const thirdMenuDown = () => {
		increaseThirdCount(thirdCount + 1);
		if (thirdCount % 2 === 0) {
			openThirdMenu(true);
		} else {
			openThirdMenu(false);
		}
	};

	const meMouseEnter = () => {
		openMeMenu(true);
	};
	const meMouseLeave = () => {
		openMeMenu(false);
	};

	const switchOpen = () => {
		props.openMenu(false);
	};

	return (
		<div className="OffCanvasFixed">
			<RB.Row className="OffCanvas">
				<p className="display-6 sign p-md-4 mt-md-0 pt-md-3 ms-0 ms-md-4">
					<span className="signName">Hayder Ali </span>
					<span className="d-lg-none d-block offCanvasOpenIcon" onClick={switchOpen}>
						{' '}
						<RB.CloseButton className="OffIcon" />{' '}
					</span>
				</p>

				<span className="OffCanvasMainIcon d-none d-md-block text-center">
					{' '}
					<RS.SiReactos />{' '}
				</span>
				<RB.Container fluid className="OffCanvasCont">
					<RB.Row className="OffCanvasrow  ms-3 pe-2">
						<RB.Col xs={12} className="OffCanvasBorder ">
							<input type="search" className="form-control" placeholder="Search" />
						</RB.Col>
					</RB.Row>
				</RB.Container>
				<div className="OffCanvasMenu mt-2 text-muted">
					<ul className="pt-4">
						<li>
							<NavLink to="/" activeclassname="Active" className="OffCanvasList">
								<span className="OffCanvasLeft">
									<RI.BsArrowRightSquare />
								</span>
								<span className="OffCanvasRight"> Dashboard</span>
							</NavLink>
						</li>
						<li>
							<NavLink to="/Trending" className="OffCanvasList">
								<span className="OffCanvasLeft">
									<RI.BsArrowRightSquare />
								</span>
								<span className="OffCanvasRight">Trending</span>
							</NavLink>
						</li>
						<li onClick={MenuDown}>
							<div className="OffCanvasList">
								<span className={`${secondMenu ? 'isRotated' : 'isNotRotated'} OffCanvasLeft`}>
									<RI.BsArrowDownSquare />
								</span>
								<span className="OffCanvasRight" style={{cursor:"pointer"}}>Shop</span>
								<div className={`${secondMenu ? 'secondMenuHello ' : 'secondMenuByee'} `}>
									<ul className={`${secondMenu ? 'secondMenuULShow' : 'secondMenuUL'} `}>
										<li>
											<NavLink to="/Shop/Electronics">Electronics</NavLink>
										</li>
										<li>
											<NavLink to="/Shop/Applainces">Applainces</NavLink>
										</li>
										<li>
											<NavLink to="/Shop/Books">Books</NavLink>
										</li>
										<li>
											<NavLink to="/Shop/Cart">Cart</NavLink>
										</li>
									</ul>
								</div>
							</div>
						</li>
						<li>
							<NavLink to="/Components" className="OffCanvasList">
								<span className="OffCanvasLeft">
									<RI.BsArrowRightSquare />
								</span>
								<span className="OffCanvasRight"> Snippets</span>
							</NavLink>
						</li>
						<li onClick={thirdMenuDown}>
							<div className="OffCanvasList">
								<span className={`${thirdMenu ? 'isRotated' : 'isNotRotated'} OffCanvasLeft`}>
									<RI.BsArrowDownSquare />
								</span>
								<span className="OffCanvasRight" style={{cursor:"pointer"}}>Courses</span>
								<div className={`${thirdMenu ? 'secondMenuHello ' : 'secondMenuByee'}`}>
									<ul className={`${thirdMenu ? 'secondMenuULShow' : 'secondMenuUL'} `}>
										<li>
											<NavLink to="/Services/Website_Development">Website Dev</NavLink>
										</li>
										<li>
											<NavLink to="/Services/Error_Detection">Remove ERR</NavLink>
										</li>
										<li>
											<NavLink to="/Services/Photoshop_Course">Photoshop</NavLink>
										</li>
										<li>
											<NavLink to="/Services/React_Course">React</NavLink>
										</li>
									</ul>
								</div>
							</div>
						</li>
						<li>
							<NavLink to="/Documentation" className="OffCanvasList">
								<span className="OffCanvasLeft">
									<RI.BsArrowRightSquare />
								</span>
								<span className="OffCanvasRight"> Libraries</span>
							</NavLink>
						</li>
						<li>
							<NavLink to="/About" className="OffCanvasList">
								<span className="OffCanvasLeft">
									<RI.BsArrowRightSquare />
								</span>
								<span className="OffCanvasRight"> About Me</span>
							</NavLink>
						</li>
					</ul>
				</div>

				<div className={`${meMenu ? ' meMenuOpen text-center ms-4 d-none d-lg-block' : 'meMenuClosed'}`}>
					<RB.Image src={me1} width={150} height={150} className="OffCanvasImg rounded" />
					<br />
					<div className="meMenuInner">
						<p className="meMenuPara">
							<span className="fs-5 ">
								Hayder Ali
								<br />
								Front-End React Developer
								<br />
							</span>
							<span className="display-6 fs-4 mt-2 mb-5 ms-2 meMenuHeader">
								Contact Me <br />
							</span>
							<span className="mt-5"> hyderali0889@gmail.com </span>
							<br />
							+92 322 6200515
						</p>
					</div>
				</div>
				<RB.Row className="OffCanvasMe" />
				<RB.Col xs={3} className="mt-2  fs-5">
					<RB.Image src={me1} width={50} height={50} className="OffCanvasImg rounded" />
				</RB.Col>
				<RB.Col xs={8} className="ms-lg-2 mt-lg-2">
					<RB.Row>
						<RB.Col xs={12} className="fs-4 meSpan">
							<p className="meMenuName" onMouseEnter={meMouseEnter} onMouseLeave={meMouseLeave}>
								Hayder Ali
							</p>
						</RB.Col>
						<RB.Col xs={12} className="fs-6 meSpan">
							React Developer
						</RB.Col>
					</RB.Row>
				</RB.Col>
			</RB.Row>
		</div>
	);
}

export default OffCanvasmenu;
