import "../assets/css/components/Border.css";
import "animate-hover/css/animate.css/animate.min.css";
import "animate-hover/css/animate-hover.min.css";
function Border(props) {
    const style={
        width:props.width,
        height:props.height
    }
    return ( 
        <div className="BorderMain text-center ms-3 ms-md-0 ahvr-animated animated-box animated-pulse" style={style}  >
         <div className="BorderText">
             {props.children}
         </div>
        </div>
     );
}

export default Border;