import React from 'react';
import ReactDOM from 'react-dom';
import HomePage from './pages/HomePage';
import Trending from './pages/Trending';
import Electronics from './pages/Shop/Electronics';
import Cart from './pages/Shop/Cart';
import Books from './pages/Shop/Books';
import Applainces from './pages/Shop/Home Applainces';
import Error from './pages/Services/Error_Detection';
import Photoshop from './pages/Services/Photoshop_Course';
import React1 from './pages/Services/React_Course';
import WebDev from './pages/Services/Website_Development';
import About from './pages/About_Me';
import Component from './pages/Components';
import Documentation from './pages/Documentation';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import NotFound from "./pages/404Page";


ReactDOM.render(
	<React.StrictMode>
		<BrowserRouter>
			<Routes>
				<Route path="/" element={<HomePage />} />
				<Route path="/Trending" element={<Trending /> } />

				<Route path="/Shop/Electronics" element={<Electronics />} /> 
				<Route path="/Shop/Books" element={ <Books /> } />
				<Route path="/Shop/Cart" element={ <Cart />  } />
				<Route path="/Shop/Applainces" element={ <Applainces /> } />

				<Route path="/Services/Website_Development" element={<WebDev />} />
				<Route path="/Services/Error_Detection" element={<Error />} />
				<Route path="/Services/Photoshop_Course" element={<Photoshop />} />
				<Route path="/Services/React_Course" element={<React1 />} />

				<Route path="/Components" element={<Component />} />
				<Route path="/About" element={<About />} />
				<Route path="/Documentation" element={<Documentation />} />
				<Route path="*" exact element={< NotFound />} />
			</Routes>
		</BrowserRouter>
	</React.StrictMode>,
	document.getElementById('root')
);
