import '../assets/css/components/SignUpModal.css';
import * as RB from 'react-bootstrap';
function SignUpModal( props ) {
	return (
		<div className="mainSignUp">
			<div className="SignUpModalMain">
				<RB.Container fluid>
					<RB.Row>
						<RB.Col sm={10}>
							{' '}
							<p className="display-6 text-primary fw-bold"> Sign Up</p>
						</RB.Col>
						<RB.Col sm={2} className="pt-2">
							{' '}
							<RB.CloseButton onClick={props.CloseSignUp}  variant="white" />
						</RB.Col>
					</RB.Row>
					<RB.Row className="mt-4">
						<RB.Col sm={5}>
							<label htmlFor="Sign" className="display-6 fw-bold fs-5 pt-1">
								Full Name{' '}
							</label>
						</RB.Col>
						<RB.Col sm={7}>
							<input type="text" id="Sign" placeholder="Your Name" className="SignUpText form-control mt-2 mt-md-0 ms-5 ms-md-0" />
						</RB.Col>
					</RB.Row>
					<RB.Row className="mt-4">
						<RB.Col sm={5}>
							<label htmlFor="SignEmail" className="display-6 fw-bold fs-5 pt-1">
								{' '}
								Email{' '}
							</label>
						</RB.Col>
						<RB.Col sm={7}>
							<input
								type="email"
								id="SignEmail"
								placeholder="Your Email"
								className="SignUpText form-control mt-2 mt-md-0 ms-5 ms-md-0"
							/>
						</RB.Col>
					</RB.Row>
					<RB.Row className="mt-4">
						<RB.Col sm={5}>
							<label htmlFor="Signpass" className="display-6 fw-bold fs-5 pt-1">
								{' '}
								Password{' '}
							</label>
						</RB.Col>
						<RB.Col sm={7}>
							<input
								type="password"
								id="Signpass"
								placeholder="Your password"
								className="SignUpText form-control  mt-2 mt-md-0 ms-5 ms-md-0"
							/>
						</RB.Col>
					</RB.Row>
					<RB.Row className="mt-4">
						<RB.Col sm={5}>
							<label htmlFor="Signrep" className="display-6 fw-bold fs-5 pt-1">
								Repeat Password{' '}
							</label>
						</RB.Col>
						<RB.Col sm={7}>
							<input
								type="password"
								id="Signrep"
								placeholder="Repeat password"
								className="SignUpText form-control mt-2 mt-md-0 ms-5 ms-md-0"
							/>
						</RB.Col>
					</RB.Row>
					<RB.Row className="w-100 mt-2">
						<RB.Col sm={12}>
							<span>
								<RB.FormCheck type="checkbox" className="" /> Agree To All{' '}
								<a href="/" onClick={(e) => e.preventDefault()}>
									{' '}
									Terms and Conditions{' '}
								</a>{' '}
							</span>
						</RB.Col>
					</RB.Row>
					<RB.Row className="w-100">
						<RB.Col sm={12} className="d-flex justify-content-center mt-2">
							<RB.Button variant="success" className="w-75">
								{' '}
								Sign Up{' '}
							</RB.Button>
						</RB.Col>
					</RB.Row>
				</RB.Container>
			</div>
		</div>
	);
}

export default SignUpModal;
