import '../assets/css/pages/Home.css';
import * as RB from 'react-bootstrap';
import * as RC from 'react-icons/cg';
import Header from '../components/Header';
import ReactDOM from 'react-dom';
import SignUpModal from '../components/SignUpModal';
import SignInModal from '../components/SignInModal';
import OffCanvasmenu from '../components/OffCanvasmenu';
import ClimbingBoxLoader from 'react-spinners/ClimbingBoxLoader';
import Footer from '../components/Footer';
import { useState, useEffect } from 'react';
import Border from '../components/Border';
import AboutImg from '../assets/images/Others/About.svg';
import MessengerCustomerChat from 'react-messenger-customer-chat';

function About() {
	const [ isMenuOpen, openMenu ] = useState(false);
	const [ isloading, setLoading ] = useState(true);
	useEffect(() => {
		setLoading(false);
	}, []);
	const openOffMenu = () => {
		openMenu(true);
		console.log(isMenuOpen);
	};
	const [ isSignUpOpen, OpenisSignUp ] = useState(false);
	const [ isSignInOpen, OpenisSignIn ] = useState(false);
	const CloseSignUp = () => {
		OpenisSignUp(false);
	};
	const CloseSignIn = () => {
		OpenisSignIn(false);
	};
	return (
		<div className={`${isloading ? 'd-flex justify-content-center Home-Loading' : ' '}`}>
			{isloading ? (
				<ClimbingBoxLoader color="#45a29e" className="HomeLoader" />
			) : (
				<RB.Container fluid className="HomeCont">
					{/* Loader Ends */}
					<RB.Row>
						{/* Main Row Starts */}
						<RB.Col
							lg={3}
							xl={3}
							className={`${isMenuOpen ? 'd-block w-md-25 smallWidth' : 'd-none'} d-lg-block noPad`}
						>
							<OffCanvasmenu openMenu={openMenu} />
						</RB.Col>
						{/* Off Canvas Menu Ends */}
						<RB.Col
							md={2}
							className={`${isMenuOpen ? 'd-none' : ' '} d-block d-lg-none position-fixed homeMenu`}
						>
							<span>
								<RC.CgMenuGridO onClick={openOffMenu} />
							</span>
						</RB.Col>
						{/* Off Canvas Mobile Button Ends */}

						{/* OffCanvas Ends */}
						<RB.Col lg={9} xs={12} className={`${isMenuOpen ? 'HomeBlured' : ' '}`}>
							<Header name="About Me" SignIn={OpenisSignIn} SignUp={OpenisSignUp} />
							{/* Main Header Ends */}
							<RB.Row className="w-100">
								<RB.Col xs={12} className="fw-bold display-6 ms-md-3 mt-4 mb-4 ">
									<Border width="100%">
										<div className="d-flex justify-content-between HomeSales">
											<p className="mt-4 HomeHeader fs-1 ms-md-5 ms-0">About Me</p>
											<p>
												<RB.Image
													src={AboutImg}
													width="200px"
													height="200px"
													className="d-none d-md-block ms-4 me-4"
												/>
												<RB.Image
													src={AboutImg}
													width="150px"
													height="150px"
													className="d-md-none me-4"
												/>
											</p>
										</div>
									</Border>
								</RB.Col>
							</RB.Row>

							<RB.Row>
								<RB.Col sm={12}>
									<Border>
										<RB.Row className="p-4">
											<RB.Col
												sm={12}
												md={6}
												style={{
													display: 'flex',
													alignSelf: 'center',
													justifyContent: 'center'
												}}
											>
												{' '}
												<span className="HomeHeader display-6 fs-1 mb-2 mb-md-0 fw-bold"> About Me </span>{' '}
											</RB.Col>
											<RB.Col sm={12} md={6}>
												{' '}
												<span className="fs-4 fw-bold">
													{' '}
													Welcome To My Website, My Name is Hayder Ali . My aim is to offer
													you Best Quality Product that show you that I really care! Not only
													have I got the trendiest PRODUCTS, but I can also guarantee that
													they are of the finest quality. I started as a small business in
													Pakistan, and my aim is to continue providing my customers with
													products that keep them happy, at prices that keep them happy. My
													customers are our top priority and through my products I work hard
													towards building long-lasting and meaningful relations with them.{' '}
												</span>{' '}
											</RB.Col>
										</RB.Row>
									</Border>
								</RB.Col>
							</RB.Row>
							<br />
							<Footer />
							<br />
						</RB.Col>
						{/* Main Col Ends */}
					</RB.Row>
					{/* Main Row Ends */}
				</RB.Container>
				//Main Container Ends
			)}

			{isSignUpOpen ? (
				ReactDOM.createPortal(<SignUpModal CloseSignUp={CloseSignUp} />, document.getElementById('SignUp'))
			) : null}
			{isSignInOpen ? (
				ReactDOM.createPortal(<SignInModal CloseSignIn={CloseSignIn} />, document.getElementById('SignIn'))
			) : null}
			<MessengerCustomerChat pageId="111296841371902" appId="3041956499423319" />
		</div>
	);
}

export default About;
