import '../assets/css/pages/Home.css';
import * as RB from 'react-bootstrap';
import * as RC from 'react-icons/cg';
import Header from '../components/Header';
import ReactDOM from 'react-dom';
import SignUpModal from '../components/SignUpModal';
import SignInModal from '../components/SignInModal';
import OffCanvasmenu from '../components/OffCanvasmenu';
import ClimbingBoxLoader from 'react-spinners/ClimbingBoxLoader';
import { useState, useEffect } from 'react';
import DocComp from "../components/DocComp";
import DocImg from "../assets/images/Others/Documentations.svg";
import Border from "../components/Border";
import apexcharts from "../assets/images/Others/apexcharts-logo.svg";
import bootstrap from "../assets/images/Others/bootstrap-brands.svg";
import facebook from "../assets/images/Others/facebook-messenger-brands.svg";
import github from "../assets/images/Others/github-brands.svg";
import RBoot from "../assets/images/Others/RB.svg";
import Reac from "../assets/images/Others/react-brands.svg";
import ricons from "../assets/images/Others/RI.svg";
import Footer from "../components/Footer";
import MessengerCustomerChat from 'react-messenger-customer-chat';

function About() {
	const [ isMenuOpen, openMenu ] = useState(false);
	const [ isloading, setLoading ] = useState(true);
	useEffect(() => {
		setLoading(false);
	}, []);
	const openOffMenu = () => {
		openMenu(true);
		console.log(isMenuOpen);
	};
	const [ isSignUpOpen, OpenisSignUp ] = useState(false);
	const [ isSignInOpen, OpenisSignIn ] = useState(false);
	const CloseSignUp = () => {
		OpenisSignUp(false);
	};
	const CloseSignIn = () => {
		OpenisSignIn(false);
	};
	return (
		<div className={`${isloading ? 'd-flex justify-content-center Home-Loading' : ' '}`}>
			{isloading ? (
				<ClimbingBoxLoader color="#45a29e" className="HomeLoader" />
			) : (
				<RB.Container fluid className="HomeCont">
					{/* Loader Ends */}
					<RB.Row>
						{/* Main Row Starts */}
						<RB.Col
							lg={3}
							xl={3}
							className={`${isMenuOpen ? 'd-block w-md-25 smallWidth' : 'd-none'} d-lg-block noPad`}
						>
							<OffCanvasmenu openMenu={openMenu} />
						</RB.Col>
						{/* Off Canvas Menu Ends */}
						<RB.Col
							md={2}
							className={`${isMenuOpen ? 'd-none' : ' '} d-block d-lg-none position-fixed homeMenu`}
						>
							<span>
								<RC.CgMenuGridO onClick={openOffMenu} />
							</span>
						</RB.Col>
						{/* Off Canvas Mobile Button Ends */}

						{/* OffCanvas Ends */}
						<RB.Col lg={9} xs={12} className={`${isMenuOpen ? 'HomeBlured' : ' '}`}>
						<Header
								name="Documentation"
								SignIn={OpenisSignIn}
								SignUp={OpenisSignUp}
							/>
							{/* Main Header Ends */}
							 <RB.Row>
								 <RB.Col sm={12} className="mt-5 mt-md-0">
                                    <Border>
									<div className="d-flex justify-content-between HomeSales">
											<p className="mt-4 HomeHeader fs-1 ms-md-5 ">Documentation</p>
											<p>
												<RB.Image
													src={DocImg}
													width="200px"
													height="200px"
													className="d-none d-md-block ms-4 me-4"
												/>
												<RB.Image
													src={DocImg}
													width="150px"
													height="150px"
													className="d-md-none me-4"
												/>
											</p>
										</div>
									</Border>
								 </RB.Col>
							 </RB.Row>

							 <RB.Row>
								 <RB.Col sm={12} md={6} className="mt-5">
									 <Border >

									 <DocComp para="React makes it painless to create interactive UIs. Design simple views for each state in your application, and React will efficiently update and render just the right components when your data changes." Header="React Library"  img={Reac} />
									 </Border>
								 </RB.Col>
								 <RB.Col sm={12} md={6} className="mt-5">
									 <Border >
									 <DocComp para="Bootstrap is a free and open-source CSS framework directed at responsive, mobile-first front-end web development. It contains CSS- and JavaScript-based design templates for typography, forms, buttons, navigation, and other interface components." Header="Bootstrap"  img={bootstrap} />
									 </Border>
								 </RB.Col>
							 </RB.Row>
							 <RB.Row>
								 <RB.Col sm={12} md={6} className="mt-5">
									 <Border>
									 <DocComp para="React component for Messenger customer chat plugin" Header="Messenger for React" img={facebook} />
									 </Border>
								 </RB.Col>
								 <RB.Col sm={12} md={6} className="mt-5">
									 <Border>
									 <DocComp para="Websites for you and your projects.
Hosted directly from your GitHub repository. Just edit, push, and your changes are live." Header="Github Pages"  img={github} />
									 </Border>
								 </RB.Col>
							 </RB.Row>
							 <RB.Row>
								 <RB.Col sm={12} md={6} className="mt-5">
									 <Border>
									 <DocComp para="React-Bootstrap replaces the Bootstrap JavaScript. Each component has been built from scratch as a true React component, without unneeded dependencies like jQuery." Header="React Bootstrap" img={RBoot} />
									 </Border>
								 </RB.Col>
								 <RB.Col sm={12} md={6} className="mt-5">
									 <Border>
									 <DocComp para="ApexCharts is loaded with powerful features to fulfill your data-visualization needs.
Look at some of the key features of ApexCharts.
ApexCharts scales gracefully in desktops, tablets as well as mobiles. You can set responsive queries and can have different layouts for different screen sizes." Header="Apex Charts"  img={apexcharts} />
									 </Border>
								 </RB.Col>
							 </RB.Row>
							 <RB.Row>
								 <RB.Col sm={12} md={6} className="mt-5">
									 <Border>
									 <DocComp para="Include popular icons in your React projects easily with react-icons, which utilizes ES6 imports that allows you to include only the icons that your project is using."  Header="React Icons" img={ricons}  />
									 </Border>
								 </RB.Col>
								 
							 </RB.Row>
							 <br />
							 <Footer />
							 <br />
						</RB.Col>
						{/* Main Col Ends */}
					</RB.Row>
					{/* Main Row Ends */}
				</RB.Container>
				//Main Container Ends
			)}

{isSignUpOpen ? (
				ReactDOM.createPortal(<SignUpModal CloseSignUp={CloseSignUp} />, document.getElementById('SignUp'))
			) : null}
			{isSignInOpen ? (
				ReactDOM.createPortal(<SignInModal CloseSignIn={CloseSignIn} />, document.getElementById('SignIn'))
			) : null}
			<MessengerCustomerChat pageId="111296841371902" appId="3041956499423319" />
		</div>
	);
}

export default About;
