import '../assets/css/components/SignInModal.css';
import * as RB from 'react-bootstrap';
function SignInModal( props ) {
	return (
		<div className="mainSignIn">
			<div className="SignInModalMain">
				<RB.Container fluid>
					<RB.Row>
						<RB.Col sm={10} className=''>
							{' '}
							<p className="display-6 text-primary fw-bold"> Sign In</p>
						</RB.Col>
						<RB.Col  sm={2} className="pt-2">
							{' '}
							<RB.CloseButton onClick={props.CloseSignIn}  variant="white" />
						</RB.Col>
					</RB.Row>

					<RB.Row className="mt-5">
						<RB.Col sm={5}>
							<label htmlFor="SignEmail" className="display-6 fw-bold fs-5 pt-1 ">
								{' '}
								Email{' '}
							</label>
						</RB.Col>
						<RB.Col sm={7}>
							<input
								type="email"
								id="SignEmail"
								placeholder="Your Email"
								className="SignUpText form-control ms-5 ms-md-0 mt-2 mt-md-0"
							/>
						</RB.Col>
					</RB.Row>
					<RB.Row className="mt-4">
						<RB.Col sm={5}>
							<label htmlFor="Signpass" className="display-6 fw-bold fs-5 pt-1">
								{' '}
								Password{' '}
							</label>
						</RB.Col>
						<RB.Col sm={7}>
							<input
								type="password"
								id="Signpass"
								placeholder="Your password"
								className="SignUpText form-control ms-5 ms-md-0 mt-2 mt-md-0"
							/>
						</RB.Col>
					</RB.Row>
                    <RB.Row className="mt-3">
                        <RB.Col sm={12} >
                            <RB.FormCheck /> Remember Me
                        </RB.Col>
                    </RB.Row>
					<RB.Row className="w-100">
						<RB.Col sm={12} className="d-flex justify-content-center mt-4">
							<RB.Button variant="primary" className="w-75">
								{' '}
								Sign In{' '}
							</RB.Button>
						</RB.Col>
					</RB.Row>
				</RB.Container>
			</div>
		</div>
	);
}

export default SignInModal;
