import '../assets/css/pages/Home.css';
import '../assets/css/pages/Trending.css';
import * as RB from 'react-bootstrap';
import * as RC from 'react-icons/cg';
import Header from '../components/Header';
import ReactDOM from 'react-dom';
import SignUpModal from '../components/SignUpModal';
import SignInModal from '../components/SignInModal';
import OffCanvasmenu from '../components/OffCanvasmenu';
import ClimbingBoxLoader from 'react-spinners/ClimbingBoxLoader';
import Border from '../components/Border';
import { TrendingData } from '../datafiles/Products/TrendingData';
import ProductMenu from '../components/ProductMenu';
import { useState, useEffect } from 'react';
import Footer from '../components/Footer';
import { Link } from 'react-router-dom';
import RangeSlider from 'react-bootstrap-range-slider';
import TrendingImage from '../assets/images/Others/Trending.svg';
import rating from '../assets/images/Others/rating.png';
import { add } from 'cart-localstorage';
import MessengerCustomerChat from 'react-messenger-customer-chat';

function Trending() {
	const [ isMenuOpen, openMenu ] = useState(false);
	const [ isloading, setLoading ] = useState(true);
	const [ isProductMenuOpen, OpenProductMenu ] = useState(false);
	const [ ProductMenuData, AddProductMenuData ] = useState(' ');
	const [ isSignUpOpen, OpenisSignUp ] = useState(false);
	const [ isSignInOpen, OpenisSignIn ] = useState(false);
	const [ FilterDivRight, OpenFilterDiv ] = useState(0);
	const [ CartItems, addCartItems ] = useState(0);
	const [ value, setValue ] = useState(0);


	useEffect(() => {
		setLoading(false);
		
	}, []);

	const openOffMenu = () => {
		openMenu(true);
	};
	const CloseSignUp = () => {
		OpenisSignUp(false);
	};
	const CloseSignIn = () => {
		OpenisSignIn(false);
	};
	const NameClick = (e) => {
		const find = TrendingData.find((f) => f.id === Number(e.target.id));
		OpenProductMenu(true);
		AddProductMenuData(find);
	};

	const AddToCart = (e) => {
		const find2 = TrendingData.find((f) => f.id === Number(e.target.id));
		AddProductMenuData(find2);
		addCartItems(CartItems + 1);
		add({id: find2.id, name: find2.first_name, price: find2.Price, Rating: find2.Rating , Image : find2.Image,width:find2.width , height:find2.height });
		
		OpenProductMenu(true);
		e.target.classList.add('disabled');
	};

	const FilterDiv = {
		position: 'fixed',
		top: 0,
		bottom: 0,
		right: 0,
		backgroundColor: '#1f2833',
		width: FilterDivRight,
		transition: '1s',
		borderLeft: '#66fcf1 1px solid'
	};

	const ApplyFilter = () => {
		OpenFilterDiv(220);
	};
	const CloseFilterDiv = () => {
		OpenFilterDiv(0);
	};
	return (
		<div className={`${isloading ? 'd-flex justify-content-center Home-Loading' : ' '}`}>
			{isloading ? (
				<ClimbingBoxLoader color="#45a29e" className="HomeLoader" />
			) : (
				<RB.Container fluid className="HomeCont">
					{/* Loader Ends */}
					<RB.Row>
						{/* Main Row Starts */}
						<RB.Col
							lg={3}
							xl={3}
							className={`${isMenuOpen ? 'd-block w-md-25 smallWidth' : 'd-none'} d-lg-block noPad`}
						>
							<OffCanvasmenu openMenu={openMenu} />
						</RB.Col>
						{/* Off Canvas Menu Ends */}
						<RB.Col
							md={2}
							className={`${isMenuOpen ? 'd-none' : ' '} d-block d-lg-none position-fixed homeMenu`}
						>
							<span>
								<RC.CgMenuGridO onClick={openOffMenu} />
							</span>
						</RB.Col>
						{/* Off Canvas Mobile Button Ends */}

						{/* OffCanvas Ends */}
						<RB.Col lg={9} xs={12} className={`${isMenuOpen ? 'HomeBlured' : ' '}`}>
							<Header name="Trending" SignIn={OpenisSignIn} SignUp={OpenisSignUp} />
							{/* Main Header Ends */}

							<RB.Row className="w-100">
								<RB.Col xs={12} className="fw-bold display-6 ms-md-3 mt-4 mb-4 ">
									<Border width="100%">
										<div className="d-flex justify-content-between HomeSales">
											<p className="mt-4 HomeHeader fs-1 ms-md-5 ms-0">Trending Products</p>
											<p>
												{' '}
												<RB.Image
													src={TrendingImage}
													width="200px"
													height="200px"
													className="d-none d-md-block ms-4 me-4"
												/>
												<RB.Image
													src={TrendingImage}
													width="150px"
													height="150px"
													className="d-md-none me-4"
												/>{' '}
											</p>
										</div>
									</Border>
								</RB.Col>
							</RB.Row>
							{/* Headerr Sections Ends */}
							<RB.Container>
								<RB.Row>
									<RB.Col sm={12}>
										<div className="d-flex justify-content-between">
											<div />
											<div className="fw-bold fs-4">
												<RB.Button variant="outline-warning" className="me-3 p-2">
													{' '}
													<Link to="/Shop/Cart"> Your Cart</Link>{' '}
													<RB.Badge bg="success" className="ms-1">
														{CartItems}
													</RB.Badge>
												</RB.Button>
												<RB.Button variant="outline-light" onClick={ApplyFilter}>
													{' '}
													Apply Filters{' '}
												</RB.Button>
											</div>
										</div>
									</RB.Col>
								</RB.Row>
							</RB.Container>
							{/* Filter Section Ends */}

							<div style={FilterDiv}>
								<div className="d-flex justify-content-between">
									<p className="display-6 ps-2 pt-3 HomeHeader fw-bold"> Filters </p>
									<RB.CloseButton className="pt-5 mt-1 me-3" onClick={CloseFilterDiv} />
								</div>
								<div className="p-2">
									<p className="display-6 fs-5"> Search </p>
									<input type="search" className="form-control" placeholder="Search" />
									<p className="display-6 fs-5 pt-4"> Search By Price </p>
									<p>
										{' '}
										<RangeSlider
											max={50000}
											variant="success"
											className="w-100"
											value={value}
											onChange={(changeEvent) => setValue(changeEvent.target.value)}
										/>{' '}
									</p>
									<p className="display-6 fs-5 pt-4"> Search By Tags </p>
									<div>
										{' '}
										<RB.FormCheck label="Electronics" /> <RB.FormCheck label="Applainces" />{' '}
										<RB.FormCheck label="Books" /> <RB.FormCheck label="Phones" />{' '}
										<RB.FormCheck label="Laptops" />{' '}
									</div>
									<RB.Button variant="outline-success btn-sm mt-4 w-100"> Search </RB.Button>
								</div>
							</div>

							{/* Filter Div Ends */}
							<RB.Row>
								<RB.Col sm={12}>
									{TrendingData.map((e) => {
										return (
											<div className="mt-5" key={e.id}>
												<Border>
													<RB.Container fluid className="pt-3 pb-3 TrendingContainer">
														<RB.Row>
															<RB.Col sm={4}>
																<RB.Image
																	src={e.Image}
																	width={e.width}
																	height={e.height}
																	className="ms-4 ms-md-0 pt-0 pt-md-3"
																/>{' '}
															</RB.Col>
															<RB.Col sm={8}>
																{' '}
																<RB.Row className="TrendingNameRow">
																	<RB.Col
																		sm={12}
																		className="display-6 fs-3 mt-5 ms-3 ms-md-0 TrendingName fw-bold"
																	>
																		{' '}
																		<span
																			className="TrendingCursor d-none d-md-block"
																			id={e.id}
																			onClick={NameClick}
																		>
																			{' '}
																			{e.first_name}{' '}
																		</span>{' '}
																		<span className="d-block d-md-none ms-4 ms-md-0"> {e.first_name} </span>
																	</RB.Col>
																</RB.Row>
																<RB.Row className="TrendingNameRow">
																	<RB.Col
																		sm={12}
																		className="fw-bold mt-3 ms-4 ms-md-0"
																	>
																		{' '}
																		<RB.Image src={rating} width={50} /> {e.Rating}{' '}
																	</RB.Col>
																</RB.Row>{' '}
																<RB.Row className="TrendingNameRow">
																	<RB.Col
																		sm={12}
																		className="fw-bold display-6 fs-4 text-danger mt-3 "
																	>
																		<input
																			type="button"
																			value={` Add To Cart For ${e.Price} $ `}
																			className=" ms-4 ms-md-0 btn btn-outline-danger"
																			onClick={AddToCart}
																			id={e.id}
																		/>{' '}
																	</RB.Col>
																</RB.Row>
															</RB.Col>
														</RB.Row>
													</RB.Container>
												</Border>
											</div>
										);
									})}
								</RB.Col>
							</RB.Row>
							<br />
							<Footer />
							<br />
						</RB.Col>
						{/* Main Col Ends */}
					</RB.Row>
					{/* Main Row Ends */}
				</RB.Container>
				//Main Container Ends
			)}

			{isSignUpOpen ? (
				ReactDOM.createPortal(<SignUpModal CloseSignUp={CloseSignUp} />, document.getElementById('SignUp'))
			) : null}
			{isSignInOpen ? (
				ReactDOM.createPortal(<SignInModal CloseSignIn={CloseSignIn} />, document.getElementById('SignIn'))
			) : null}
			{isProductMenuOpen ? (
				<ProductMenu CloseProduct={OpenProductMenu} ProductMenuData={ProductMenuData} />
			) : null}
			<MessengerCustomerChat pageId="111296841371902" appId="3041956499423319" />
		</div>
	);
}

export default Trending;
