import P404 from '../assets/images/Others/404.png';
import * as RB from 'react-bootstrap';
import { Link } from 'react-router-dom';
function NotFound() {
	const style = {
		height: '100vh',
		display: 'flex',
		justifyContent: 'space-between',
		marginTop: '50px'
	};
	return (
		<div style={style}>
			<RB.Image src={P404} height={'450px'} width={'500px'} className='d-none d-md-block' />
            <RB.Image src={P404} height={'150px'} width={'200px'} className='d-block d-md-none' />


			<span className="display-6 text-primary fw-bold me-5 pe-5">
				{' '}
				<br /> This Page is a Ghost <br />{' '}
				<p className="text-secondary fw-bold fs-md-2 fs-6 mt-4 ">
					{' '}
					The boat had looked good to the naked eye. But you wear a very strong prescription and should have
					been wearing glasses. As you cling on to the bouey the coast guard had thrown at you, you watch the
					water rush into you beloved dingy. The leak sprays water higher and higher. Then the boat was
					swallowed and sunk into the abyss.{' '}
				</p>
				
				<p className="text-warning fs-6 fs-md-2">
					{' '}
					Sorry But We Could Not Find What You are looking For
					<br />
					<Link to="/" className="mt-md-3 d-flex justify-content-center">
						{' '}
						<RB.Button variant="outline-success" className='btn-sm w-100 h-25'> Go To Home Page </RB.Button>{' '}
					</Link>
				</p>
			</span>
		</div>
	);
}

export default NotFound;
